import BaseApiService from "services/BaseApiService";

const DEFAULT_PATH = "/custom/api/acl/role/manage";

class RolesService extends BaseApiService {
  getAll = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/search?start=${start}&limit=${limit}&${search}`;
    return this.makeGetRequestWithAuth(url);
  };
  save = (obj: any) => {
    const url = `${DEFAULT_PATH}`;
    return this.makePostRequestWithAuth(url, obj);
  };
  approve = (obj: any) => {
    const url = "/custom/api/approval/request";
    return this.makePostRequestWithAuth(url, obj);
  };
  update = (obj: any, uid: string | undefined) => {
    const url = `${DEFAULT_PATH}/${uid}`;
    return this.makePutRequestWithAuth(url, obj);
  };
  delete = (uid: string) => {
    const url = `${DEFAULT_PATH}/${uid}`;
    return this.makeDeleteRequestWithAuth(url);
  };
  findById = (id: any) => {
    const url = `${DEFAULT_PATH}/uid/${id}`;
    return this.makeGetRequestWithAuth(url);
  };
  activate = (uid: string) => {
    const url = `${DEFAULT_PATH}/activate/${uid}`;
    return this.makePutRequestWithAuth(url);
  };
  Inactivate = (uid: string) => {
    const url = `${DEFAULT_PATH}/inactivate/${uid}`;
    return this.makePutRequestWithAuth(url);
  };
  getMenuForTreeTableComponent = () => {
    const url = "/auth/api/acl/feature/search?limit=1000&isActive=true";
    return this.makeGetRequestWithAuth(url);
  };

  //modelToAPIParam = () => {};
}

export default new RolesService();
